import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { getAuth, updateProfile, updatePassword, EmailAuthProvider, reauthenticateWithCredential } from 'firebase/auth';
import { db } from '../firebase';
import { doc, getDoc, updateDoc } from 'firebase/firestore';
import './MyPage.css';

function MyPage() {
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');
  const [userId, setUserId] = useState('');
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [passwordSuccess, setPasswordSuccess] = useState('');
  const navigate = useNavigate();
  const auth = getAuth();

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const user = auth.currentUser;
        if (!user) {
          navigate('/login');
          return;
        }

        const userDoc = await getDoc(doc(db, 'users', user.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          setName(userData.name || '');
          setPhone(userData.phone || '');
          setEmail(userData.email || '');
          setUserId(userData.userId || '');
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
        setError('   .');
      }
    };

    fetchUserData();
  }, [auth, navigate]);

  const handleProfileUpdate = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    try {
      const user = auth.currentUser;
      if (!user) {
        navigate('/login');
        return;
      }

      // Firebase Auth  
      await updateProfile(user, {
        displayName: name
      });

      // Firestore   
      await updateDoc(doc(db, 'users', user.uid), {
        name,
        phone,
        updatedAt: new Date().toISOString()
      });

      setSuccess('  .');
    } catch (error) {
      console.error('Error updating user data:', error);
      setError('  .');
    }
  };

  const handlePasswordUpdate = async (e) => {
    e.preventDefault();
    setPasswordError('');
    setPasswordSuccess('');

    if (newPassword !== confirmNewPassword) {
      setPasswordError('   .');
      return;
    }

    if (newPassword.length < 6) {
      setPasswordError(' 6  .');
      return;
    }

    try {
      const user = auth.currentUser;
      if (!user) {
        navigate('/login');
        return;
      }

      //   
      const credential = EmailAuthProvider.credential(
        user.email,
        currentPassword
      );
      await reauthenticateWithCredential(user, credential);

      //  
      await updatePassword(user, newPassword);

      setPasswordSuccess('  .');
      setCurrentPassword('');
      setNewPassword('');
      setConfirmNewPassword('');
    } catch (error) {
      console.error('Error updating password:', error);
      if (error.code === 'auth/wrong-password') {
        setPasswordError('   .');
      } else {
        setPasswordError('  .');
      }
    }
  };

  return (
    <div className="my-page-container">
      <h2>My Page</h2>
      
      <form onSubmit={handleProfileUpdate} className="my-page-form">
        <h3> </h3>
        <div className="form-group">
          <label htmlFor="userId"></label>
          <input
            type="text"
            id="userId"
            value={userId}
            disabled
          />
        </div>
        <div className="form-group">
          <label htmlFor="email"></label>
          <input
            type="email"
            id="email"
            value={email}
            disabled
          />
        </div>
        <div className="form-group">
          <label htmlFor="name"></label>
          <input
            type="text"
            id="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="phone"></label>
          <input
            type="tel"
            id="phone"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            required
          />
        </div>
        {error && <div className="error-message">{error}</div>}
        {success && <div className="success-message">{success}</div>}
        <button type="submit"> </button>
      </form>

      <form onSubmit={handlePasswordUpdate} className="my-page-form">
        <h3> </h3>
        <div className="form-group">
          <label htmlFor="currentPassword"> </label>
          <input
            type="password"
            id="currentPassword"
            value={currentPassword}
            onChange={(e) => setCurrentPassword(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="newPassword"> </label>
          <input
            type="password"
            id="newPassword"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="confirmNewPassword">  </label>
          <input
            type="password"
            id="confirmNewPassword"
            value={confirmNewPassword}
            onChange={(e) => setConfirmNewPassword(e.target.value)}
            required
          />
        </div>
        {passwordError && <div className="error-message">{passwordError}</div>}
        {passwordSuccess && <div className="success-message">{passwordSuccess}</div>}
        <button type="submit"> </button>
      </form>
    </div>
  );
}

export default MyPage; 