import React, { useState, useEffect } from 'react';
import { collection, addDoc, getDocs, query, orderBy, deleteDoc, doc, getDoc, Timestamp } from 'firebase/firestore';
import { auth, db } from '../firebase';
import { useNavigate } from 'react-router-dom';
import './Board.css';

function Board() {
  const [posts, setPosts] = useState([]);
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [isAdmin, setIsAdmin] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    checkAdminStatus();
    fetchPosts();
  }, []);

  const checkAdminStatus = async () => {
    if (auth.currentUser) {
      try {
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          // userId 'admin'   
          setIsAdmin(userData.userId === 'admin');
        }
      } catch (error) {
        console.error('Error checking admin status:', error);
      }
    }
    setLoading(false);
  };

  const fetchPosts = async () => {
    try {
      const postsRef = collection(db, 'posts');
      const q = query(postsRef, orderBy('createdAt', 'desc'));
      const querySnapshot = await getDocs(q);
      
      const postList = [];
      querySnapshot.forEach((doc) => {
        postList.push({
          id: doc.id,
          ...doc.data()
        });
      });
      
      setPosts(postList);
    } catch (error) {
      console.error('Error fetching posts:', error);
      setError('    .');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!auth.currentUser) {
      setError(' .');
      return;
    }

    // userId 'admin'   
    const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
    if (!userDoc.exists() || userDoc.data().userId !== 'admin') {
      setError('    .');
      return;
    }

    if (!title.trim() || !content.trim()) {
      setError('   .');
      return;
    }

    setLoading(true);
    setError('');

    try {
      //   
      const userData = userDoc.data();
      
      const postData = {
        title,
        content,
        authorId: auth.currentUser.uid,
        authorName: userData.userName || userData.userId || '',
        createdAt: Timestamp.now()
      };

      await addDoc(collection(db, 'posts'), postData);
      
      setTitle('');
      setContent('');
      
      fetchPosts();
    } catch (error) {
      console.error('Error creating post:', error);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (postId) => {
    // userId 'admin'   
    const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
    if (!userDoc.exists() || userDoc.data().userId !== 'admin') {
      setError('    .');
      return;
    }

    if (!window.confirm('  ?')) {
      return;
    }

    try {
      await deleteDoc(doc(db, 'posts', postId));
      fetchPosts();
    } catch (error) {
      console.error('Error deleting post:', error);
      setError('    .');
    }
  };

  const formatDate = (date) => {
    if (!date) return '';
    
    // Firestore Timestamp  
    if (date.toDate && typeof date.toDate === 'function') {
      const d = date.toDate();
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`;
    }
    
    //  Date  
    if (date instanceof Date) {
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
    }
    
    // ISO  
    if (typeof date === 'string') {
      const d = new Date(date);
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`;
    }
    
    //  
    return '  ';
  };

  if (loading) {
    return <div className="loading"> ...</div>;
  }

  return (
    <div className="board-container">
      <h2></h2>
      
      {isAdmin && (
        <div className="post-form-container">
          <h3>  </h3>
          <form onSubmit={handleSubmit} className="post-form">
            <div className="form-group">
              <label htmlFor="title"></label>
              <input
                type="text"
                id="title"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                placeholder="  "
                className="form-control"
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="content"></label>
              <textarea
                id="content"
                value={content}
                onChange={(e) => setContent(e.target.value)}
                placeholder="  "
                className="form-control"
                rows="5"
              />
            </div>
            
            {error && <div className="error-message">{error}</div>}
            
            <button 
              type="submit" 
              className="submit-button"
              disabled={loading}
            >
              {loading ? ' ...' : ' '}
            </button>
          </form>
        </div>
      )}
      
      <div className="posts-list">
        <h3> </h3>
        {posts.length === 0 ? (
          <p className="no-posts">  .</p>
        ) : (
          <div className="posts-table">
            <table>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  {isAdmin && <th></th>}
                </tr>
              </thead>
              <tbody>
                {posts.map((post, index) => (
                  <tr key={post.id}>
                    <td>{posts.length - index}</td>
                    <td>
                      <span 
                        className="post-title"
                        onClick={() => navigate(`/board/post/${post.id}`)}
                      >
                        {post.title}
                      </span>
                    </td>
                    <td>{post.authorName}</td>
                    <td>{formatDate(post.createdAt)}</td>
                    {isAdmin && (
                      <td>
                        <button 
                          className="delete-button"
                          onClick={() => handleDelete(post.id)}
                        >
                          
                        </button>
                      </td>
                    )}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}

export default Board; 