// client/src/components/Signup.js

import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { createUserWithEmailAndPassword, updateProfile, getAuth } from 'firebase/auth';
import { db } from '../firebase';
import { doc, setDoc, getDocs, query, collection, where, Timestamp } from 'firebase/firestore';
import './Signup.css';

function Signup() {
  const [userId, setUserId] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [error, setError] = useState('');
  const [isIdAvailable, setIsIdAvailable] = useState(true);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const auth = getAuth();

  const checkUserIdExists = async (id) => {
    if (!id) {
      setIsIdAvailable(true);
      return;
    }

    try {
      const q = query(collection(db, 'users'), where('userId', '==', id));
      const querySnapshot = await getDocs(q);
      setIsIdAvailable(querySnapshot.empty);
    } catch (error) {
      console.error('Error checking user ID:', error);
      setIsIdAvailable(false);
    }
  };

  useEffect(() => {
    const timer = setTimeout(() => {
      checkUserIdExists(userId);
    }, 500);

    return () => clearTimeout(timer);
  }, [userId]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    if (password !== confirmPassword) {
      setError('  .');
      setLoading(false);
      return;
    }

    if (!name) {
      setError(' .');
      setLoading(false);
      return;
    }

    if (!phone) {
      setError(' .');
      setLoading(false);
      return;
    }

    try {
      //   
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(email)) {
        throw new Error('   .');
      }

      //   
      if (password.length < 6) {
        throw new Error('  6  .');
      }

      // userId   (, ,  )
      const userIdRegex = /^[a-zA-Z0-9_]+$/;
      if (!userIdRegex.test(userId)) {
        throw new Error(' , , (_)  .');
      }

      //  
      if (!name.trim()) {
        throw new Error(' .');
      }

      //   
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;

      // Firestore   
      await setDoc(doc(db, 'users', user.uid), {
        email,
        name,
        phone,
        userId,
        isAdmin: userId === 'admin', // userId 'admin'   
        createdAt: Timestamp.now()
      });

      //   
      await updateProfile(user, {
        displayName: name
      });

      navigate('/main');
    } catch (error) {
      console.error('Error signing up:', error);
      if (error.code === 'auth/email-already-in-use') {
        setError('   .');
      } else if (error.code === 'auth/weak-password') {
        setError(' 6  .');
      } else {
        setError('   .');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="signup-container">
      <h2></h2>
      <form onSubmit={handleSubmit} className="signup-form">
        <div className="form-group">
          <label htmlFor="userId"></label>
          <input
            type="text"
            id="userId"
            value={userId}
            onChange={(e) => setUserId(e.target.value)}
            required
            className={userId && !isIdAvailable ? 'error-input' : ''}
          />
          {userId && !isIdAvailable && (
            <div className="error-message">   .</div>
          )}
          {userId && isIdAvailable && (
            <div className="success-message">  .</div>
          )}
        </div>
        <div className="form-group">
          <label htmlFor="name"></label>
          <input
            type="text"
            id="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="phone"></label>
          <input
            type="tel"
            id="phone"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            placeholder="010-0000-0000"
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="email"></label>
          <input
            type="email"
            id="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="password"></label>
          <input
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="confirmPassword"> </label>
          <input
            type="password"
            id="confirmPassword"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
          />
        </div>
        {error && <div className="error-message">{error}</div>}
        <button type="submit" disabled={!isIdAvailable || loading}></button>
        <Link to="/login" className="login-link"></Link>
      </form>
    </div>
  );
}

export default Signup;