import React, { useState, useEffect } from 'react';
import { collection, addDoc, getDocs, query, orderBy, deleteDoc, doc, getDoc } from 'firebase/firestore';
import { auth, db } from '../firebase';
import './VideoUpload.css';

function VideoUpload() {
  const [youtubeUrl, setYoutubeUrl] = useState('');
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [videos, setVideos] = useState([]);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    checkAdminStatus();
    fetchVideos();
  }, []);

  const checkAdminStatus = async () => {
    if (auth.currentUser) {
      try {
        const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
        if (userDoc.exists()) {
          const userData = userDoc.data();
          setIsAdmin(userData.userId === 'admin');
        }
      } catch (error) {
        console.error('Error checking admin status:', error);
      }
    }
  };

  const fetchVideos = async () => {
    try {
      const videosRef = collection(db, 'videos');
      const q = query(videosRef, orderBy('createdAt', 'desc'));
      const querySnapshot = await getDocs(q);
      
      const videoList = [];
      querySnapshot.forEach((doc) => {
        videoList.push({
          id: doc.id,
          ...doc.data()
        });
      });
      
      setVideos(videoList);
    } catch (error) {
      console.error('Error fetching videos:', error);
      setError('    .');
    }
  };

  const extractVideoId = (url) => {
    const regExp = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
    const match = url.match(regExp);
    return (match && match[2].length === 11) ? match[2] : null;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!auth.currentUser) {
      setError(' .');
      return;
    }

    //   
    if (!isAdmin) {
      setError('    .');
      return;
    }

    if (!youtubeUrl) {
      setError(' URL .');
      return;
    }

    const videoId = extractVideoId(youtubeUrl);
    if (!videoId) {
      setError('  URL .');
      return;
    }

    setLoading(true);
    setError('');

    try {
      //   
      const userDoc = await getDoc(doc(db, 'users', auth.currentUser.uid));
      const userData = userDoc.exists() ? userDoc.data() : { userId: '' };
      
      const videoData = {
        userId: auth.currentUser.uid,
        userName: userData.userName || userData.userId || '',
        youtubeUrl,
        videoId,
        title: title || ' ',
        description: description || '',
        createdAt: new Date()
      };

      await addDoc(collection(db, 'videos'), videoData);
      
      setYoutubeUrl('');
      setTitle('');
      setDescription('');
      
      fetchVideos();
    } catch (error) {
      console.error('Error uploading video:', error);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (videoId) => {
    if (!auth.currentUser) {
      setError(' .');
      return;
    }

    //   
    if (!isAdmin) {
      setError('    .');
      return;
    }

    if (!window.confirm('  ?')) {
      return;
    }

    try {
      await deleteDoc(doc(db, 'videos', videoId));
      fetchVideos();
    } catch (error) {
      console.error('Error deleting video:', error);
      setError('    .');
    }
  };

  return (
    <div className="video-upload-container">
      <h2> </h2>
      
      {isAdmin && (
        <form onSubmit={handleSubmit} className="video-upload-form">
          <div className="form-group">
            <label htmlFor="youtubeUrl"> URL</label>
            <input
              type="text"
              id="youtubeUrl"
              value={youtubeUrl}
              onChange={(e) => setYoutubeUrl(e.target.value)}
              placeholder="  URL "
              className="form-control"
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="title"> ()</label>
            <input
              type="text"
              id="title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="  "
              className="form-control"
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="description"> ()</label>
            <textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="   "
              className="form-control"
              rows="3"
            />
          </div>
          
          {error && <div className="error-message">{error}</div>}
          
          <button 
            type="submit" 
            className="submit-button"
            disabled={loading}
          >
            {loading ? ' ...' : ''}
          </button>
        </form>
      )}
      
      <div className="videos-list">
        <h3> </h3>
        {videos.length === 0 ? (
          <p className="no-videos">  .</p>
        ) : (
          <div className="video-grid">
            {videos.map((video) => (
              <div key={video.id} className="video-card">
                <div className="video-embed">
                  <iframe
                    width="100%"
                    height="200"
                    src={`https://www.youtube.com/embed/${video.videoId}`}
                    title={video.title}
                    frameBorder="0"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowFullScreen
                  />
                </div>
                <div className="video-info">
                  <h4>{video.title}</h4>
                  <p className="video-description">{video.description}</p>
                  <p className="video-meta">
                    : {video.userName} | 
                    {video.createdAt && new Date(video.createdAt.toDate()).toLocaleDateString()}
                  </p>
                  {isAdmin && (
                    <button 
                      className="delete-button"
                      onClick={() => handleDelete(video.id)}
                    >
                      
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

export default VideoUpload; 