import React, { useState, useEffect } from 'react';
import { auth, db } from '../firebase';
import { doc, getDoc, updateDoc } from 'firebase/firestore';
import { updatePassword, EmailAuthProvider, reauthenticateWithCredential } from 'firebase/auth';
import { deleteUser } from 'firebase/auth';
import { useNavigate } from 'react-router-dom';
import './Profile.css';

const Profile = () => {
  const [userData, setUserData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState('');
  const [phone, setPhone] = useState('');
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const user = auth.currentUser;
        if (!user) {
          navigate('/login');
          return;
        }

        const userDoc = await getDoc(doc(db, 'users', user.uid));
        if (userDoc.exists()) {
          const data = userDoc.data();
          setUserData(data);
          setPhone(data.phone || '');
        }
        setLoading(false);
      } catch (err) {
        console.error('Error fetching user data:', err);
        setError('     .');
        setLoading(false);
      }
    };

    fetchUserData();
  }, [navigate]);

  const handlePhoneUpdate = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    try {
      const user = auth.currentUser;
      if (!user) return;

      await updateDoc(doc(db, 'users', user.uid), {
        phone: phone
      });

      setSuccess('  .');
    } catch (err) {
      console.error('Error updating phone:', err);
      setError('    .');
    }
  };

  const handlePasswordUpdate = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    if (newPassword !== confirmPassword) {
      setError('     .');
      return;
    }

    try {
      const user = auth.currentUser;
      if (!user) return;

      //   
      const credential = EmailAuthProvider.credential(
        user.email,
        currentPassword
      );
      await reauthenticateWithCredential(user, credential);

      //  
      await updatePassword(user, newPassword);

      setSuccess('  .');
      setCurrentPassword('');
      setNewPassword('');
      setConfirmPassword('');
    } catch (err) {
      console.error('Error updating password:', err);
      if (err.code === 'auth/wrong-password') {
        setError('   .');
      } else {
        setError('    .');
      }
    }
  };

  const handleDeleteAccount = async () => {
    if (!window.confirm('  ?     .')) {
      return;
    }

    setError('');
    setSuccess('');

    try {
      const user = auth.currentUser;
      if (!user) return;

      //   
      await deleteUser(user);

      navigate('/login');
    } catch (err) {
      console.error('Error deleting account:', err);
      setError('    .');
    }
  };

  if (loading) return <div className="loading"> ...</div>;

  return (
    <div className="profile-container">
      <h1> </h1>
      
      {error && <div className="error-message">{error}</div>}
      {success && <div className="success-message">{success}</div>}
      
      <div className="profile-section">
        <h2> </h2>
        <div className="info-item">
          <label>:</label>
          <span>{userData?.email}</span>
        </div>
        <div className="info-item">
          <label>:</label>
          <span>{userData?.name || '-'}</span>
        </div>
        <div className="info-item">
          <label>:</label>
          <span>{userData?.createdAt ? new Date(userData.createdAt.seconds * 1000).toLocaleDateString() : '-'}</span>
        </div>
      </div>
      
      <div className="profile-section">
        <h2> </h2>
        <form onSubmit={handlePhoneUpdate}>
          <div className="form-group">
            <label htmlFor="phone">:</label>
            <input
              type="text"
              id="phone"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              placeholder=": 010-1234-5678"
            />
          </div>
          <button type="submit" className="update-button"> </button>
        </form>
      </div>
      
      <div className="profile-section">
        <h2> </h2>
        <form onSubmit={handlePasswordUpdate}>
          <div className="form-group">
            <label htmlFor="currentPassword"> :</label>
            <input
              type="password"
              id="currentPassword"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="newPassword"> :</label>
            <input
              type="password"
              id="newPassword"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              required
            />
          </div>
          <div className="form-group">
            <label htmlFor="confirmPassword">  :</label>
            <input
              type="password"
              id="confirmPassword"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              required
            />
          </div>
          <button type="submit" className="update-button"> </button>
        </form>
      </div>
      
      <div className="profile-section">
        <h2> </h2>
        <p>     .</p>
        <button onClick={handleDeleteAccount} className="delete-button"> </button>
      </div>
    </div>
  );
};

export default Profile; 