import React, { useEffect, useRef, useState } from 'react';
import './Location.css';

const Location = () => {
  const mapRef = useRef(null);
  const markerRef = useRef(null);
  const [mapLoaded, setMapLoaded] = useState(false);
  const [mapError, setMapError] = useState(null);
  const address = "   53 6-17";

  const handleFindWay = () => {
    const encodedAddress = encodeURIComponent(address);
    window.open(`https://map.kakao.com/link/to/,36.3456789,127.3456789`);
  };

  useEffect(() => {
    const initializeMap = () => {
      try {
        const container = mapRef.current;
        const options = {
          center: new window.kakao.maps.LatLng(36.3456789, 127.3456789),
          level: 3
        };
        
        const map = new window.kakao.maps.Map(container, options);
        
        markerRef.current = new window.kakao.maps.Marker({
          position: options.center
        });
        markerRef.current.setMap(map);
        
        const geocoder = new window.kakao.maps.services.Geocoder();
        geocoder.addressSearch(address, (result, status) => {
          if (status === window.kakao.maps.services.Status.OK) {
            const coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);
            markerRef.current.setPosition(coords);
            map.setCenter(coords);
          }
        });

        setMapLoaded(true);
      } catch (error) {
        console.error("    :", error);
        setMapError("    .");
      }
    };

    const loadKakaoMap = () => {
      const script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = `//dapi.kakao.com/v2/maps/sdk.js?appkey=26d8640fa64f99a40fc037fb0e5bb873&libraries=services&autoload=false`;
      
      script.onload = () => {
        window.kakao.maps.load(() => {
          initializeMap();
        });
      };

      script.onerror = () => {
        setMapError('  .');
      };

      document.head.appendChild(script);
    };

    //   API   
    if (window.kakao && window.kakao.maps) {
      initializeMap();
    } else {
      loadKakaoMap();
    }

    return () => {
      if (markerRef.current) {
        markerRef.current.setMap(null);
      }
    };
  }, [address]);

  return (
    <div className="location-container">
      <h2 className="location-title"> </h2>
      <div className="location-content">
        <div className="map" ref={mapRef}>
          {!mapLoaded && <div className="map-loading">  ...</div>}
          {mapError && <div className="map-error">{mapError}</div>}
        </div>
        
        <div className="info-section">
          <div className="address-info">
            <p>
              <strong>:</strong> {address}
            </p>
            <p>
              <strong>:</strong> 010-4404-2584
            </p>
            <button className="find-way-btn" onClick={handleFindWay}>
              
            </button>
          </div>

          <div className="transport-section">
            <h3></h3>
            <div className="transport-info">
              <h4></h4>
              <ul>
                <li> 2   10</li>
              </ul>

              <h4></h4>
              <ul>
                <li>4  </li>
                <li>: 116, 117, 121</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Location; 