// client/src/components/MainMenu.js

import React, { useState, useEffect, useRef } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { signOut } from 'firebase/auth';
import { auth } from '../firebase';
import './MainMenu.css';

function MainMenu({ isAdmin }) {
  const location = useLocation();
  const navigate = useNavigate();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const menuRef = useRef(null);
  const [showSubmenu, setShowSubmenu] = useState(false);
  const submenuRef = useRef(null);

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      setIsLoggedIn(!!user);
    });

    //    
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        setIsMenuOpen(false);
      }
      if (submenuRef.current && !submenuRef.current.contains(event.target)) {
        setShowSubmenu(false);
      }
    };

    //   
    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      unsubscribe();
      //   
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const handleLogout = async () => {
    try {
      await signOut(auth);
      navigate('/login');
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleLogin = () => {
    navigate('/login');
    setIsMenuOpen(false);
  };

  const handleReservationClick = (e) => {
    if (!isLoggedIn) {
      e.preventDefault();
      localStorage.setItem('redirectAfterLogin', '/reservation');
      navigate('/login');
    } else {
      navigate('/reservation');
    }
    setIsMenuOpen(false);
  };

  const handleMenuClick = (path) => {
    navigate(path);
    setIsMenuOpen(false);
    setShowSubmenu(false);
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const toggleSubmenu = () => {
    setShowSubmenu(!showSubmenu);
  };

  //      
  const handleAdminMenuClick = (path) => {
    navigate(path);
    setIsMenuOpen(false);
    setShowSubmenu(false);
  };

  //     
  const userName = auth.currentUser?.displayName || auth.currentUser?.email || '';

  console.log('MainMenu isAdmin:', isAdmin);
  console.log('MainMenu isAdmin type:', typeof isAdmin);

  return (
    <nav className="navbar">
      <div className="container" ref={menuRef}>
        <Link to="/main" className="navbar-brand" onClick={() => setIsMenuOpen(false)}>
           
        </Link>
        
        <button className="navbar-toggler" onClick={toggleMenu}>
          <span className="navbar-toggler-icon"></span>
        </button>
        
        <div className={`navbar-collapse ${isMenuOpen ? 'show' : ''}`}>
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link 
                to="/introduction" 
                className={`nav-link ${location.pathname === '/introduction' ? 'active' : ''}`}
                onClick={() => handleMenuClick('/introduction')}
              >
                
              </Link>
            </li>
            <li className="nav-item">
              <Link 
                to="/reservation" 
                className={`nav-link ${location.pathname === '/reservation' ? 'active' : ''}`}
                onClick={handleReservationClick}
              >
                 
              </Link>
            </li>

            <li className="nav-item">
              <Link 
                to="/video-upload" 
                className={`nav-link ${location.pathname === '/video-upload' ? 'active' : ''}`}
                onClick={() => handleMenuClick('/video-upload')}
              >
                 
              </Link>
            </li>
            <li className="nav-item">
              <Link 
                to="/board" 
                className={`nav-link ${location.pathname === '/board' ? 'active' : ''}`}
                onClick={() => handleMenuClick('/board')}
              >
                
              </Link>
            </li>
            <li className="nav-item">
              <Link 
                to="/location" 
                className={`nav-link ${location.pathname === '/location' ? 'active' : ''}`}
                onClick={() => handleMenuClick('/location')}
              >
                 
              </Link>
            </li>
            {isAdmin && (
              <li className="admin-menu">
                <span> </span>
                <ul className="admin-dropdown">
                  <li><Link to="/member-info" onClick={() => handleAdminMenuClick('/member-info')}></Link></li>
                </ul>
              </li>
            )}
          </ul>
          
          <div className="nav-user">
            {isLoggedIn ? (
              <>
                <div className="user-info" ref={submenuRef}>
                  <span className="user-name" onClick={toggleSubmenu}>
                    {userName}
                    <span className="dropdown-arrow"></span>
                  </span>
                  {showSubmenu && (
                    <div className="submenu">
                      <Link to="/profile" className="submenu-item" onClick={() => handleMenuClick('/profile')}> </Link>
                      <Link to="/my-reservations" className="submenu-item" onClick={() => handleMenuClick('/my-reservations')}></Link>
                      <button onClick={handleLogout} className="logout-button">
                        
                      </button>
                    </div>
                  )}
                </div>
              </>
            ) : (
              <button onClick={handleLogin} className="btn-login">
                
              </button>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}

export default MainMenu;